/* ini css halaman stunting calculate */
.stunting-calculate {
  background-image: linear-gradient(
    to bottom,
    var(--primary-color),
    rgba(255, 255, 255, 0.5)
  );
}

.stunting_form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-group {
  width: 100%;
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

label {
  font-size: 16px;
  margin-bottom: 5px;
