  margin-top: 1rem;
  font-weight: 400 !important;
}

.news_author {
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.news_author img {
  max-width: 30px;
  border-radius: 100%;
}

.news_author p {
  font-size: 0.9rem;
  opacity: 0.75;
}

.news_content h4 {
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.news_footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
